<script>
import TariffComp from "@/components/TariffComp.vue";

export default {
  components: {
    TariffComp,
  },
  data() {
    name: "MainPage";
    return {
      tabBarBtn: [
        { id: "1", desription: "Features" },
        { id: "2", desription: "Tech specs" },
      ],
      activeBtn: "1",
    };
  },
  methods: {
    setActiveTab(id) {
      this.activeBtn = id;
    },
  },
};
</script>

<style lang="sass" scoped>

.main
    padding-bottom: 64px
    position: relative
    &__breadcrumbs
       display: flex
       flex-direction: row
       font-size: 14px
       line-height: 145%
       gap: 16px
       color: $selectColor
       padding-bottom: 16px

       p:last-of-type
           color: $copyrightColor

       span
           color: $copyrightColor
    &__wrapper
        display: flex
        flex-direction: row
        &_img
            max-width: 406px
            max-height: 585px
    &__content
        display: flex
        flex-direction: column
        padding-left: 96px
        gap: 24px
        width: 100%
        &_title
            display: flex
            flex-direction: row
            align-items: center
            h1
                color: $selectColor
                font-size: 36px
                line-height: 120%
                padding-left: 16px
        &_tabs
            display: flex
            gap: 8px
            &_btn
                @include buttonTab
                &:hover
                  transition: 0.5s
                  background-color: $baseColor
                &:active
                  transition: 0.1s
                  transform: scale(1.1)
                &.active
                    color: #272E3C
                    background-color: #fff
        &_list
            font-size: 14px
            line-height: 140%
            li
                list-style-type: disc
                list-style-position: inside
                color: $copyrightColor
        &_btn
            @include buttonFont
            width: 100%
            height: 56px
            cursor: pointer
            &:hover
              background-color: $selectColor
              color: $baseColor
              transition: ease 0.5s
            &:active
              transition: 0.1s
              background-color: $baseColor
              color: $selectColor
        &_secure
          display: flex
          justify-content: center
          color: #6F7388
          gap: 8px
</style>
